














import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MBody',
  props: {
    size: {
      type: String,
      default: 'medium',
      validator(size) {
        return ['', 'x-small', 'small', 'medium', 'large', 'x-large'].includes(
          size
        );
      }
    },
    color: {
      type: String,
      default: '',
      validator(color) {
        return [
          '',
          'ink',
          'ink-light',
          'ink-lighter',
          'white',
          'indigo'
        ].includes(color);
      }
    },
    weight: {
      type: String,
      default: ''
    },
    lineClamp: {
      type: Number,
      default: 0
    }
  },
  computed: {
    lineClampClassName() {
      return this.lineClamp > 0
        ? {
            display: '-webkit-box',
            overflow: 'hidden',
            'max-height': 'initial',
            '-webkit-line-clamp': `${this.lineClamp}`,
            '-webkit-box-orient': 'vertical'
          }
        : {};
    }
  }
});
