

















import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MInputText',
  props: {
    inputType: {
      type: String,
      default: 'text',
      validator(inputType: string) {
        return ['text', 'tel', 'url', 'password', 'search'].includes(inputType);
      }
    },
    name: {
      type: String,
      default: ''
    },
    value: {
      type: [String, Number],
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    maxlength: {
      type: Number,
      default: undefined
    },
    type: {
      type: String,
      default: ''
    },
    height: {
      type: String,
      default: ''
    },
    width: {
      type: String,
      default: ''
    },
    minWidth: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    blockName() {
      return this.type ? `m-input-text-${this.type}` : 'm-input-text';
    }
  },
  methods: {
    onChange(event) {
      if (event.target instanceof HTMLInputElement) {
        this.$emit('input', event.target.value);
      }
    },
    keydownEvent() {
      this.$emit('keydown');
    },
    focusEvent() {
      this.$emit('focus');
    }
  }
});
