
























import Vue from 'vue';
import MIcon from '../MIcon/MIcon.vue';
import { Data, Methods, Computed, Props } from './types';
export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MButton',
  components: {
    MIcon
  },
  props: {
    size: {
      type: String,
      default: 'medium',
      validator(size: string) {
        return ['', 'small', 'medium', 'large'].includes(size);
      }
    },
    maxWidth: {
      type: String,
      default: ''
    },
    minWidth: {
      type: String,
      default: ''
    },
    height: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'standard',
      validator(type: string) {
        return ['primary', 'standard', 'outlined', 'icon-rounded'].includes(
          type
        );
      }
    },
    iconType: {
      type: String,
      default: 'fas'
    },
    iconName: {
      type: String,
      default: ''
    },
    iconSize: {
      type: String,
      default: ''
    },
    iconColor: {
      type: String,
      default: ''
    },
    icon: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    customStyle: {
      type: Object,
      default: null
    }
  },
  computed: {
    buttonStyle() {
      const style = {
        'max-width': this.maxWidth,
        'min-width': this.minWidth,
        height: this.height
      };
      return this.customStyle ? { ...style, ...this.customStyle } : style;
    },
    listeners() {
      return { ...this.$listeners };
    }
  }
});
