




















































































import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
import MButton from '../MButton/MButton.vue';
import MLabel from '../MTypography/MLabel/MLabel.vue';
import MImage from '../MImage/MImage.vue';
import MIcon from '../MIcon/MIcon.vue';

export default Vue.extend<Data, Methods, Computed, Props>({
  components: {
    MButton,
    MLabel,
    MImage,
    MIcon
  },
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    limitSize: {
      validator: prop => typeof prop === 'number' || prop === null,
      default: null
    },
    allowType: {
      type: [String, Array],
      default: () => []
    },
    accept: {
      type: String,
      default: ''
    },
    unit: {
      type: String,
      default: 'MB'
    },
    maxHeight: {
      type: String,
      default: undefined
    },
    maxWidth: {
      type: String,
      default: undefined
    },
    minWidth: {
      type: String,
      default: undefined
    },
    minHeight: {
      type: String,
      default: undefined
    },
    height: {
      type: String,
      default: undefined
    },
    width: {
      type: String,
      default: undefined
    },
    imagePath: {
      type: String,
      default: undefined
    },
    disabled: {
      type: Boolean,
      default: false
    },
    aspectRatio: {
      type: Object,
      default: () => ({ x: 1, y: 1 })
    }
  },
  data() {
    return {
      files: [],
      errorMessages: [],
      isDrag: false,
      byteSize: {
        KB: 1024,
        MB: 1048576
      },
      cropper: false,
      croppedImage: null
    };
  },
  computed: {
    findFileImagePath() {
      let image;
      this.files.find(item => {
        if (item.image) {
          image = item.image;
        }
      });
      return image;
    },
    setlimitSize() {
      return this.limitSize * this.byteSize[this.unit];
    },
    inputFileStyle() {
      const style = {
        'max-width': this.maxWidth,
        'min-width': this.minWidth,
        'max-height': this.maxHeight,
        'min-height': this.minHeight,
        width: this.width,
        height: this.height
      };
      return this.customStyle ? { ...style, ...this.customStyle } : style;
    }
  },
  methods: {
    async setUploadFile(event) {
      this.errorMessages = [];
      const files = event.target.files || event.dataTransfer.files;
      const file = files[0];
      if (this.checkFile(file)) {
        this.createImage(file);
        this.$emit('uploadFile', file);
      }
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = event => {
        const object = {
          image: event.target.result,
          name: file.name
        };
        object.image = event.target.result;
        object.name = file.name;
        this.files.push(object);
        this.$emit('crop', object.image);
      };
      reader.readAsDataURL(file);
    },
    onPreviewClose(item) {
      const index = this.files.indexOf(item);
      this.files.splice(index, 1);
      this.$emit('deleteImage');
    },
    onDrag() {
      this.isDrag = true;
    },
    offDrag() {
      this.isDrag = false;
    },
    dropFile(event) {
      this.setUploadFile(event);
      this.offDrag();
      this.$emit('crop', '');
    },
    checkFile(file) {
      let result = true;
      this.eroorMessages = [];
      // キャンセルしたら処理中断
      this.allowType.forEach((element, i) => {
        if (this.allowType.indexOf(file.type) === -1) {
          result = false;
          this.errorMessages.push(
            `アップロードできるのは${this.allowType[i]}ファイルです。`
          );
        } else if (this.allowType === []) {
          result = true;
        } else {
          result = true;
        }
      });
      if (this.files.length > 9) {
        this.errorMessages.push(
          `アップロードできるイメージファイルは10ファイルまでです。`
        );
        result = false;
        this.$emit('checkFile');
      }
      const fileSize = parseInt(file.size);
      if (fileSize > this.setlimitSize) {
        this.errorMessages.push(
          `アップロードできるファイルサイズは${this.limitSize}${this.unit}までです。`
        );
        result = false;
      } else if (this.limitSize === null) {
        result = true;
      }
      return result;
    },
    openCropper() {
      this.cropper = true;
    },
    closeCropper() {
      this.cropper = false;
    },
    cropImage() {
      this.croppedImage = this.$refs.cropper.getCroppedCanvas().toDataURL();
      this.$emit('crop', this.croppedImage);
    },
    resetCropPosition() {
      this.$refs.cropper.reset();
    }
  }
});
