




















import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MTag',
  props: {
    type: {
      type: String,
      default: '',
      validator(type: string) {
        return ['', 'circle', 'square', 'dot'].includes(type);
      }
    },
    label: {
      type: String,
      default: ''
    },
    status: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: ''
    },
    active: {
      type: Boolean,
      default: false
    },
    bold: {
      type: Boolean,
      default: false
    }
  }
});
