














import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MIcon',
  props: {
    iconType: {
      type: String,
      default: 'fas',
      validator(iconType: string) {
        return ['fas'].includes(iconType);
      }
    },
    iconName: {
      type: String,
      default: '',
      required: true,
      validator(iconName: string) {
        return [
          '',
          'angle-up',
          'angle-right',
          'angle-down',
          'angle-left',
          'arrow-up',
          'arrow-down',
          'arrow-left',
          'arrow-right',
          'bell',
          'briefcase',
          'building',
          'columns',
          'copy',
          'calendar-alt',
          'check-square',
          'check-circle',
          'check-double',
          'eye',
          'external-link-alt',
          'exclamation-triangle',
          'file-alt',
          'inbox',
          'info-circle',
          'image',
          'lightbulb',
          'pause-circle',
          'plus-circle',
          'search',
          'square',
          'tv',
          'tools',
          'trash-alt',
          'times',
          'user',
          'users'
        ].includes(iconName);
      }
    },
    size: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: ''
    },
    customColor: {
      type: String,
      default: ''
    },
    innerClass: {
      type: String,
      default: ''
    },
    customStyle: {
      type: Object,
      default: null
    }
  },
  computed: {
    iconStyle() {
      const style = {
        'font-size': this.size,
        color: this.customColor
      };
      return this.customStyle ? { ...style, ...this.customStyle } : style;
    }
  }
});
