






























import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
import MIcon from '../MIcon/MIcon.vue';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MLink',
  components: {
    MIcon
  },
  props: {
    tag: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: ''
    },
    size: {
      type: String,
      default: 'medium',
      validator(size: string) {
        return ['', 'x-small', 'small', 'medium', 'large'].includes(size);
      }
    },
    iconName: {
      type: String,
      default: 'angle-right'
    },
    to: {
      type: String,
      default: ''
    },
    href: {
      type: String,
      default: ''
    },
    activeClass: {
      type: String,
      default: ''
    },
    exactActiveClass: {
      type: String,
      default: ''
    },
    icon: {
      type: Boolean,
      default: false
    },
    bold: {
      type: Boolean,
      default: false
    },
    underline: {
      type: Boolean,
      default: false
    },
    blank: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    LinkType() {
      return this.tag === 'anchor' ? 'a' : 'router-link';
    }
  }
});
