












import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MHeading',
  props: {
    level: {
      type: Number,
      validator(level: number) {
        return [1, 2, 3, 4, 5, 6].includes(level);
      },
      default: 1
    },
    visual: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: ''
    }
  },
  computed: {
    tagLevel() {
      return `h${this.level}`;
    },
    visualLevel() {
      return this.visual ? this.visual : this.level;
    }
  }
});
