












































import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
import MIcon from '../MIcon/MIcon.vue';
import MInputText from '../MInputText/MInputText.vue';
import ClickOutside from 'vue-click-outside';

const KEYCODE = {
  ARROW_UP: 38,
  ARROW_DOWN: 40,
  TAB: 9
};

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'MSelectbox',
  components: {
    MIcon,
    MInputText
  },
  directives: {
    ClickOutside
  },
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    initialSelectedValue: {
      type: [String, Number],
      default: ''
    },
    selectedValue: {
      type: [String, Number],
      default: ''
    },
    selectList: {
      type: Array,
      default: () => []
    },
    itemText: {
      type: String,
      default: ''
    },
    minWidth: {
      type: String,
      default: ''
    },
    inputDisabled: {
      type: Boolean,
      default: false
    },
    buttonDisabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showSelectList: false
    };
  },
  computed: {
    setSelectedValue() {
      return this.selectedValue === '' || this.selectedValue === 0
        ? this.initialSelectedValue
        : this.selectedValue;
    },
    buttonElements() {
      return this.$refs.button;
    }
  },
  methods: {
    clickSelectbox() {
      if (this.inputDisabled === true) {
        return false;
      }
      this.showSelectList = !this.showSelectList;
      this.focusButtonElements();
    },
    keydownSelectbox(event) {
      if (this.showSelectList === false) {
        return false;
      }
      if (event.keyCode === KEYCODE.ARROW_DOWN) {
        this.showSelectList = true;
        this.focusButtonElements();
      } else if (event.keyCode === KEYCODE.TAB) {
        this.closeSelectList();
      }
    },
    keydownSelectValue(event) {
      if (event.keyCode === KEYCODE.ARROW_DOWN) {
        this.moveNext();
        event.preventDefault();
      } else if (event.keyCode === KEYCODE.ARROW_UP) {
        this.movePrev();
        event.preventDefault();
      } else if (event.keyCode === KEYCODE.TAB) {
        this.closeSelectList();
      }
    },
    findIndex(target) {
      return [].findIndex.call(this.buttonElements, event => event === target);
    },
    moveFocus(index) {
      if (this.buttonElements[index]) {
        this.buttonElements[index].focus();
      }
    },
    moveNext() {
      const index = this.findIndex(event.target);
      this.moveFocus(index + 1);
    },
    movePrev() {
      const index = this.findIndex(event.target);
      this.moveFocus(index - 1);
    },
    focusButtonElements() {
      const findIndexSelected = this.selectList.findIndex(
        item =>
          item === this.setSelectedValue ||
          item[this.itemText] === this.setSelectedValue
      );
      this.$nextTick(() => {
        if (findIndexSelected >= 0) {
          event.preventDefault();
          this.buttonElements[findIndexSelected].focus();
        } else {
          event.preventDefault();
          this.buttonElements[0].focus();
        }
      });
    },
    closeSelectList() {
      this.showSelectList = false;
    },
    changeSelectValue(item) {
      this.closeSelectList();
      this.$emit('change', item);
    }
  }
});
